.site-bar-item {
    margin-top: 20px;
    &__content{
        padding-top: 10px;
        &--item{
            padding: 15px 0;
            display: flex;
            border-bottom: 1px solid #d9d9d9;
            &:last-child{
                border-bottom: none;
            }
        }

        &--description{
            max-width: 62.9%;
            padding-left: 15px;
            font-family: Averta-bold;
            color: #404040;
            p{
                margin: 0 0 5px;
                line-height: 1.3;
            }
            .date{
                font-family: Averta-regularitalic;
                color: #99a93a;
            }
        }

        .margin-none{
            margin: 0;
        }
        
    }

    &__title{
        h4{
            font-family: Averta-bold;
            font-size: 22px;
            color: #39451e;
            text-transform: uppercase;
            padding-bottom: 13px;
            border-bottom: 2px dashed #39451e;
            margin: 0;
        }
    }
    .pt5{
        padding-top: 5px;
    }
}
