.news-infor {
    &__content{
        li{
            line-height: 20px;
            margin-right: 15px;
            span{
                line-height: 20px;
                vertical-align: middle;
                font-family: Averta-regularitalic;
                font-size: 16px;
                color: #424242;
            }

            img{
                margin-right: 9px;
            }

            a{
                color: #9cac3b;
            }
        }
    }

    &__share{
        margin: 20px 0 30px;
        ul{
            li:nth-child(1){
                margin-right: 15px;
            }
            li{
                line-height: 24px;
                height: 24px;
                vertical-align: top;
            }

            a{
                display: block;
                line-height: 24px;
                height: 24px;
                margin-right: 10px;
                img{
                    vertical-align: top;
                }
            }
        }
    }
}
