/*********************
    - 2. General -
*********************/

html,
body {
  margin: 0px;
  padding: 0px;
  font-family: Averta;
  font-size: 18px;
  zoom: 0;
}

.wrapper{
  width: 100%;
  overflow: hidden;
}

.list-inline {
  margin: 0px;
  padding: 0px;
  list-style: none;
}

.list-inline li {
  @include dis(middle);
}

a {
  color: inherit;
}

img {
  max-width: 100%;
  border: none;
}

.fleft {
  float: left;
}

.fright {
  float: right;
}

.clear-fix,
.clearfix {
  clear: both;
}

.answer{
  position: relative;
  padding: 15px;
}

.checked{
  border: 1px solid transparent;
  padding: 3px;
  border-radius: 50%;
  width: 20px !important;
  height: 20px;
  text-align: center;
  transition: all 0.3s ease;
}

label{
  display: inline-block;
}

input[type="radio"]{
  position: absolute;
  opacity: 0;
}

input[type="radio"]:checked ~ .checked{
  opacity: 1;
  border-color: #007bff;
  background-color: #007bff;
  color: #fff;
}

.form{
  padding: 50px;
  border: 1px solid #333;
  border-radius: 5px;
  margin-top: 20px;
}

.input-base{
  line-height: 35px;
  height: 35px;
  border: 1px solid #333;
  border-radius: 5px;
  position: relative;
}

.error{
  position: absolute;
  top: 35px;
  color: red;
}

.input-base:focus{
  outline: none;
}

.form-row{
  padding: 15px 0;
}

.disabled{
  display: none !important;
}

.btn-base{
  padding: 4px;
  background-color: #39451e;
  display: inline-block;
  transition: all 0.5s ease;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  text-decoration: none;
  border: 2px solid transparent;
  cursor: pointer;
  span{
    border: 2px solid #fff;
    height: 44px;
    line-height: 40px;
    padding: 0 25px;
    display: inherit;
    color: #fffefe;
  }

  &:hover{
    border-color: #39451e;
    background-color: #fff;
    span{
      color: #39451e;
      border-color: #39451e;
      font-weight: 700;
    }
  }
}

.border-tree{
  position: relative;
  padding-bottom: 30px;
  margin-bottom: 10px;
  &::after{
    content: '';
    height: 30px;
    background-image: url(images/TopBar_Banner/canhcay.png);
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    position: absolute;
    width: 100px;
  }
}

.title-base{
  font-size: 48px;
  font-family: Averta-bold;
  color: #39451e;
  margin: 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.2;
}

.over-text{
  overflow: hidden;
  position: relative;
}

textarea{
  resize: none;
}
a{
  cursor: pointer;
}
a:hover{
  color: #9cac3b;
}