.group-product-item {
    margin-top: 20px;
    padding: 12px;
    border: 3px dashed #dadada;
    transition: all 0.3s ease;
    &:hover{
        border-color: #39451e;
    }

    &__content{
        &--title{
            a{
                color: #39451e;
                text-transform: uppercase;
                font-family: Averta-bold;
                font-size: 22px;
                line-height: 60px;
                height: 60px;
                text-align: center;
                display: block;
                text-decoration: none;
            }
        }
        &--description{
            p{
                margin-bottom: 12px;
                color: #3f3f3f;
                text-align: center;

            }
        }
    }
}
