@media only screen and (max-width: 1600px){
    .contact__content--infor{
        padding: 30px 150px 0 60px;
    }
}

@media only screen and (max-width: 1400px){
    .shop{
        .shop-item2{
            span{
                font-size: 16px;
                letter-spacing: -1px;
            }
        }
        &__site-bar{
            padding: 0;
            &--title{
                p{
                    font-size: 17px;    
                }
            }
        }

        .product-selling{
            &__img{
                flex: 0 0 33.5%;
            }

            &__infor{
                padding-left: 10px;
                padding-top: 0;
                .price{
                    font-size: 18px;
                    .unit{
                        font-size: 14px;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width:1366px){
    html,
    body {
        font-size: 14px;
    }

    .category-item{
        &__title{
            p{
                font-size: 26px;
            }
        }
    }

    .buy-item{
        &__content{
            &--price{
                font-size: 40px;
            }
        }
    }

    .title-base{
        font-size: 34px;
    }

    .news-item{
        &__content{
            &--date{
                font-size: 13px;
            }
        }
    }

    .footer{
        .border-footer{
            &::after{
                top: 25px;
            }
        }

        &__infor2{
            padding: 20px 10px;
        }
    }

    .footer--title{
        font-size: 18px;
    }

    .header__logo{
        left: 25px;
    }

    .header__social{
        right: 25px;
    }

    .blog-post{
        &__breadcrumb{
            background-size: 100% 112%;
        }
    }

    .blog-post{
        &__content{
            &--title{
                font-size: 26px;
            }
        }
    }

    .policies{
        &__content{
            &--list{
                li{
                    a{
                        font-size: 16px;
                    }
                }
            }
        }
    }

    .contact-item{
        &__content{
            padding: 24px 30px;
        }
    }

    .shop{
        &__advisory{
            &--infor{
                p{
                    font-size: 24px;
                }

                .phone{
                    font-size: 44px;
                }
            }

            &--button{
                .btn-base{
                    span{
                        padding: 0 25px;
                    }
                }
            }
        }
        .shop-item2{
            span{
                // font-size: 13px;
            }
        }
    }

    .hot-product-item{
        &__content{
            &--price{
                p{
                    font-size: 22px;
                }
            }
        }
    }

    .product-detail__top--infor{
        .top-item__quantity--number{
            line-height: 45px;
            height: 45px;
            &>span{
                letter-spacing: -1px;
            }
            .quantity-number{
                height: 45px;
                vertical-align: top;
                margin-right: 10px;
                
                &__minus, &__plus{
                    line-height: 43px;
                    height: 45px;
                    width: 45px;
                    font-size: 16px;
                    vertical-align: middle;
                }

                input{
                    line-height: 43px;
                    height: 45px;
                    max-width: 65px;
                    font-size: 16px;
                    vertical-align: middle;
                }
            }
        }
        .top-item__quantity--title{
            p{
                line-height: 45px;
                height: 45px;
            }
        }

        .top-item__origin{
            &--description{
                padding-right: 0;
            }
            &--title{
                line-height: 42px;
                height: 42px;
                p{
                    line-height: 42px;
                    height: 42px;
                }
            }
        }
        
    }

    .product-detail__main--tab li a{
        font-size: 16px;
    }
}

@media only screen and (max-width:1024px){
    .header{
        &__logo{
            img{
                max-width: 60%;
            }
        }

        &__menu{
            ul{
                a{
                    font-size: 18px;
                }
            }
        }
    }

    .banner__img{
        .slider__main{
            .item{
                margin-top: 100px;
            }

            .owl-dots{
                bottom: 25px;
            }
        }
    }

    .banner{
        &__main{
            &--title{
                h1{
                    font-size: 40px;
                }
            }
        }

        &__content{
            top: 55%;
        }
    }

    .category-item{
        &__content{
            padding: 70px 15px;
        }
    }

    .about-us{
        padding: 125px 0;
    }

    .contact{
        padding: 125px 0;
        &__content{
            &--infor{
                padding: 30px 150px 0 60px;
            }
        }
    }
    
}

@media only screen and (max-width: 768px){

    .product-detail__img{
        padding-right: 0;
    }

    .product-detail__top--infor{
        padding-left: 0;
    }
    .blog-post{
        &__content{
            &--title{
                font-size: 22px;
            }
        }
    }
    .header{
        &__content{
            padding: 0 15px;
            .header-mobile__button{
                display: block !important;
                position: absolute;
                line-height: 24px;
                height: 24px;
                top: 50%;
                transform: translateY(-50%);
                cursor: pointer;
                img{
                    width: 24px;
                }
            }
        }
    }

    .mobile-hidden{
        display: none !important;
    }

    .mobile-active{
        display: block !important;
    }

    .header{
        &__logo{
            left: 50%;
            transform: translateX(-50%);
            img{
                max-width: 100%;
            }
        }
    }

    .banner__img{
        .slider__main{
            .item{
                height: 500px;
                &>img{
                    height: 100%;
                }
            }
        }
    }

    .banner{
        &__content{
            padding: 50px 15px;
        }

        &__main{
            &--title{
                h1{
                    font-size: 34px;
                }
            }
        }
    }

    .group-product,
    .highlight,
    .hot-product{
        padding: 70px 0;
    }

    .title-base{
        font-size: 28px;
    }

    .category-item{
        &__content{
            padding: 30px 0;
        }
    }

    .buy{
        .buy-item{
            margin: 20px auto;
        }

        .col-lg-4:nth-child(1){
            .buy-item{
                margin-top: -40px;
            }
        }

        .col-lg-4:nth-child(3){
            .buy-item{
                margin-bottom: -40px;
            }
        }
    }

    .about{
        padding: 70px 0;
        .col-lg-6:nth-child(1){
            order: 2;
        }
        .col-lg-6:nth-child(2){
            order: 1;
        }

        &__img{
            img{
                width: 100%;
            }
        }
    }

    .quality{
        &__formula{
            padding: 70px 0 0;
        }

        &__content{
            padding: 0 0 70px;
        }
    }

    .news{
        padding: 70px 0;
        &-item{
            &__content{
                max-width: 55%;
                margin: 0 auto 20px;
                &--img{
                    text-align: center;
                }
                &--title{
                    a{
                        white-space: nowrap; 
                        overflow: hidden;
                        text-overflow: ellipsis;
                        height: 25px;
                        display: block !important;
                    }
                }

                &--description{
                    height: 55px;
                    margin: 10px 0;
                }
            }
        }
    }

    .contact{
        padding: 70px 0px;
        &__content{
            &--infor{
                padding: 30px 0 30px 60px;
                .location{
                    p{
                        line-height: 40px;
                        height: 40px;
                    }
                }
            }
        }
    }

    .footer{
        &-item:nth-child(1){
            order: 1;
        }
        &-item:nth-child(2){
            order: 2;
        }
        &-item:nth-child(3){
            order: 4;
            margin-top: 20px;
        }
        &-item:nth-child(4){
            order: 3;
            margin-top: 20px;
        }
    }

    .why-choose{
        .container{
            padding: 0;
            max-width: 100%;
            .col-12{
                padding: 0;
            }
        }

        &::after{
            display: none;
        }

        &__content{
            padding: 70px 40px;
        }
        padding-top: 400px;
        background-size: contain;
    }

    .category .col-lg-4:nth-child(1) .category-item{
        margin-right: 0;
    }

    .category .col-lg-4:nth-child(3) .category-item{
        margin-left: 0;
    }

    .category .col-lg-4:nth-child(2) .category-item{
        margin: 0;
    }

    .connect-me{
        right: 0;
        bottom: 35px;
        .phone{
            max-width: 60%;
        }
        .message{
            max-width: 60%;
        }
    }

    .contact-item{
        .col-lg-4{
            order: 2;
        }
        .col-lg-8{
            order: 1;
        }
    }

    .contact-item{
        &__content{
            &--infor{
                padding-right: 200px;
            }
        }
    }

    .about-us{
        &__team{
            padding: 80px 0;
            &--content2{
                background: none;
            }

            &--description{
                padding: 0 50px;
            }

            
        }

        &__introduce{
            &--img{
                img{
                    width: 100%;
                    max-height: 200px;
                }
            }

            .mr-7{
                margin-right: 0;
            }

            .ml-7{
                margin-left: 0;
            }
        }

        &__why-choose{
            padding-top: 400px;
            background-size: contain;
            .col-12{
                padding: 0;
            }

            .container{
                padding: 0;
                max-width: 100%;
            }
        }
    }

    .policies{
        &__content{
            &--list{
                li{
                    a{
                        img{
                            display: initial;
                        }
                    }
                    a.active{
                        background-color: #9cac3b;
                    }
                }
            }
            .tab-content{
                &__content{
                    padding: 15px 20px 50px;
                }
            }
        }
    }

    .shop{
        &__advisory{
            &--infor{
                p{
                    font-size: 18px;
                }

                .phone{
                    font-size: 30px;
                }
            }

            &--button{
                padding: 20px 20px 20px 0;
                .btn-base{
                    span{
                        padding: 0 15px;
                        font-size: 14px;
                    }
                }
            }
        }

        &__main{
            margin: 0;
        }
    }

    .product-detail{
        &__img{
            img{
                width: 100%;
            }
        }

        &__top{
            &--infor{
                margin-top: 20px;
                .top-item__origin--description{
                    padding-right: 140px;
                }
            }
        }

        &__main{
            &--tab{
                display: inline-block;
                li{
                    display: block;
                    a{
                        border: 1px solid #39451e;
                    }
                }
                li+li{
                    padding: 0;
                    a{
                        border-top: none;
                    }
                }
            }
            .tab-content{
                &__main{
                    padding: 10px 15px;
                }

                &__table{
                    padding: 0;
                }
            }

            .review{
                padding: 50px 15px;
                &__title{
                    font-size: 30px;
                }
            }
        }
    }
}

@media only screen and (max-width:667px){
    .about-us{
        &__why-choose{
            padding-top: 350px;
        }
    }
}

@media only screen and (max-width: 520px){
    .why-choose{
        padding-top: 250px;
    }

    .about-us{
        &__why-choose{
            padding-top: 250px;
        }
    }

    .contact-item{
        &__content{
            &--infor{
                padding-right: 0;
            }
        }
    }

    .gallery{
        .mr10,.mt10,.ml10{
            margin: 0 0 10px;
        }
    }

    .news-item{
        &__content{
            max-width: 100%;
        }
    }

    .banner__content{
        display: none;
    }

    .footer{
        &-item:nth-child(1){
            order: 1;
        }
        &-item:nth-child(2){
            order: 2;
            margin-top: 20px;
        }
        &-item:nth-child(3){
            order: 3;
            margin-top: 20px;
        }
        &-item:nth-child(4){
            order: 4;
            margin-top: 20px;
        }
    }

    .faq-item__tab p{
        padding: 0 15px;
    }

    .faq-item__content p{
        padding: 15px;
    }

    .gallery img{
        height: auto;
    }

    .about-us{
        &__team{
            &--description{
                padding: 0;
            }
        }
    }

    .shop{
        &__advisory{
            &--infor{
                text-align: center;
            }

            &--button{
                text-align: center !important;
                padding: 0 0 20px;
            }
        }

        .product-selling{
            &__img{
                flex: 0 0 15%;
            }
        }

        &__main{
            &--top{
                margin-top: 15px;
                height: auto;
                line-height: 30px;
                padding-bottom: 10px;
                .text-right{
                    text-align: left !important;
                }
            }
        }
    }
}

@media only screen and (max-width:480px){
    .hot-product{
        .list-tab{
            li{
                margin-bottom: 10px;
            }
        }
    }

    .product-detail{

        &__top{
            &--infor{
                .top-item__origin--description{
                    padding-right: 0;
                }
                .top-item__quantity{
                    &--top{
                        padding-bottom: 50px;
                    }
                    &--number{
                        
                        .quantity-number{
                            display: block;
                        }
                    }
                }
                .top-item__origin--title, .top-item__quantity--title{
                    flex: 0 0 95px;
                    p{
                        font-size: 14px;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width:414px){
    .hot-product .list-tab li{
        padding: 0;
    }

    .highlight--infor .number{
        font-size: 60px;
    }

    .blog{
        &__content{
            &--pagination{

                a{
                    height: 30px;
                    width: 30px;
                    line-height: 28px;
                    font-size: 14px;
                }
            }
        }
    }

    .about-us{
        &__why-choose{
            padding-top: 200px;
        }
    }

    .news-item2{
        &__title{
            a{
                font-size: 22px;
            }
        }
    }

    .product-detail{
        &__main{
            .review{
                &__title{
                    font-size: 24px;
                }
            }
        }
        &__other{
            &--title{
                p{
                    font-size: 24px;
                }
            }

            &--description{
                font-size: 13px;
            }
        }
    }
}

@media only screen and (max-width:375px){
    .contact__content--infor .location p{
        line-height: 1.3;
    }

    .why-choose{
        padding-top: 175px;
    }

    .banner__img .slider__main .item{
        height: 300px;
    }

    .group-product__description{
        max-width: 100%;
    }

    .hot-product .list-tab li:last-child{
        margin-top: 10px;
    }

    .btn-base{
        font-size: 16px;
        span{
            padding: 0 30px;
        }
    }

    .news-item2{
        .btn-viewmore{
            span{
                padding: 0 30px;
            }
        }
    }

    .footer{
        &__contact{
            padding: 0;
        }
    }

    .policies{
        &__content{
            &--list{
                li{
                    a{
                        img{
                            max-width: 30px;
                        }
                    }
                }
            }
        }
    }

    .product-detail__top--infor{
        .top-item{
            &__name, &__price{
                font-size: 30px;
            }
        }
    }
}

@media only screen and (max-width:320px){
    .faq-item__tab p,
    .faq-item__content p{
        font-size: 13px;
    }

    .news-item2{
        &__title{
            h2{
                font-size: 24px;
            }
        }
    }

    .blog-post{
        &__quote{
            padding: 30px 15px;
            &::before{
                top: 15px;
                left: 15px;
            }
        }

        &__img{
            &--description{
                p{
                    font-size: 12px;
                }
            }
        }
    }

    .about-us{
        &__why-choose{
            padding-top: 175px;
        }
        &__team{
            &--description{
                padding: 0;
            }
        }
    }
    .shop{
        &__advisory{
            &--infor{
                padding: 20px 5px;
            }

            &--button{
                .btn-base{
                    span{
                        line-height: 31px;
                        height: 35px;
                    }
                }
            }
        }
    }

    .product-detail{
        &__main{
            .review{
                &__title{
                    font-size: 20px;
                }

                &__item{
                    &--name{
                        display: block;
                    }
                }

                &__form{
                    .btn-base{
                        span{
                            padding: 0 20px;
                        }
                    }
                }
            }
        }
        &__other{
            &--title{
                p{
                    font-size: 20px;
                }
            }
        }
    }
}