.faq-item2 {
    &__tab{
        border: 2px solid #d9d9d9;
        padding: 17px 25px;
        margin-top: 20px;
        transition: all 0.3s ease;
        cursor: pointer;
        p{
            margin: 0;
            color: #39451e;
            font-family: Averta-bold; 
            transition: all 0.3s ease;
            // &:hover{
            //     color: #39451e;
            //     background-color: #fff;
            // }
        }
        a{
            color: #404040;
            transition: all 0.3s ease;
            &:hover{
            color: #9cac3b;
            }
        }

        &:hover{
            border-color: #9cac3b;
            p{
                color: #9cac3b
            }

            a{
                color: #9cac3b
            }
        }
    }

    &__content{
        display: none;
        p{
            margin: 12px 0 0;
            padding: 15px 23px 20px;
            border: 2px dashed #d9d9d9;
            color: #404040;
        }
    }
}

.faq-item2__tab.active{
    border-color: #9cac3b;
    p{
        color: #9cac3b
    }

    a{
        color: #9cac3b
    }
}