.buy-item {
    background-image: url(images/sanphamnoibat/boxgiay.png);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    margin: -40px 0;
    padding: 20px 27px;
    max-width: 354px;
    &__content{
        border: 3px dashed #39451e;
        padding: 45px 15px;
        &--name{
            p{
                font-family: Averta-bold;
                font-size: 22px;
                color: #39451e;
                line-height: 1.1;
                margin-bottom: 30px;
            }
        }
        &--price{
            font-size: 50px;
            font-family: Averta-bold;
            color: #39451e;
            line-height: 1.15;
            margin: 0;
        }

        &--periodic{
            font-family: Averta-regularitalic;
            color: #666666;
            margin-bottom: 30px;
        }

        &--goods{
            p{
                color: #3f3f3f;
                line-height: 1.05;
            }
        }

        &--button{
            margin-top: 30px;
        }
    }
}