.category-item {
    background-image: url(images/sanphamnoibat/bgrxamke.png);
    background-size: cover;
    background-repeat: no-repeat;
    padding: 20px;
    position: relative;
    &__content{
        padding: 90px 77px;
        border: 3px dashed transparent;
        position: relative;
        z-index: 888;
        
    }
    &__bg{
        position: absolute;
        top: 0;
        left: 0;
        background-color: rgba(0,0,0,0.05);
        transition: all 0.3s ease;
    }

    &__button{
        a{
            font-size: 18px;
            font-family: Averta-bold;
            color: #39451e;
            text-transform: uppercase;
        }
    }

    &__title{
        p{
            font-family: Averta-bold;
            font-size: 30px;
            color: #39451e;
            text-transform: uppercase;
            margin: 10px 0 0;
        }
    }
}

.category-item:hover .category-item__bg{
    width: 100%;
    height: 100%;
}

.category-item:hover .category-item__content{
    border-color:  #39451e;
}
