.news-item2 {
    &__content{
        padding-top: 20px;
        margin-bottom: 40px;
    }

    &__title{
        a{
            font-size: 30px;
            font-family: Averta-bold;
            color: #39451e;
            text-transform: uppercase;
            margin: 30px 0 10px;
        }
    }

    &__description{
        p{
            color: #3f3f3f;
            margin: 25px 0 30px;
        }
    }

    .btn-viewmore span{
        padding: 0 50px;
    }

    &__img{
        img{
            width: 100%;
            max-height: 500px;
        }
    }
}
