// header
.header{
    position: fixed;
    width: 100%;
    z-index: 1999;
    &__content{
        line-height: 100px;
        height: 100px;
        vertical-align: middle;
        background-color: #fff;
        padding: 0 50px;
        position: relative;
    }
    &__logo{
        position: absolute;
        top: 0;
        left: 50px;
        img{
            max-height: 235px;
        }
    }

    &__social{
        position: absolute;
        top: 0;
        right: 50px;
        li{
            vertical-align: top;
            font-size: 16px;
        }
    }

    &__menu{
        ul {
            font-family: Averta-bold;
            li{
                vertical-align: top;
                position: relative;
                &:hover{
                    .drop-menu{
                        top: 100px;
                        opacity: 1;
                        z-index: 99;
                        pointer-events: inherit;
                    }
                }
            }

            li.active{
                &>a{
                    color: #9cac3a;
                }
            }
            a{
                text-transform: uppercase;
                text-decoration: none;
                transition: all 0.3s ease;
                color: #39451e;
                cursor: pointer;
                font-weight: 700;
                padding: 0 15px;
                font-size: 20px;
                &:hover{
                    color: #9cac3a;
                }
            }

            .drop-button.show{
                color: #9cac3a;
            }
            .drop-menu{
                margin: 0;
                padding: 0;
                position: absolute;
                z-index: 99;
                background-color: #fff;
                width: 225px;
                text-align: left;
                opacity: 0;
                z-index: -1;
                left: 0;
                -webkit-transition: all 0.35s ease;
                -o-transition: all 0.35s ease;
                -moz-transition: all 0.35s ease;
                transition: all 0.35s ease;
                top: 150px;
                pointer-events: none;
                li{
                    display: block;
                    padding: 0 10px;
                    
                    a{
                        font-family: Averta;
                        line-height: 53px;
                        display: block;
                        height: 53px;
                        padding: 0 10px;
                        text-align: left;
                        border-bottom: 1px solid #ebebeb;
                        text-transform: inherit;
                        font-weight: 400;
                        letter-spacing: -1px;
                    }
                }
            }
        }
    }
}

