    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.banner{
    position: relative;
    &__img{
        .slider__main{
            .owl-dots{
                position: absolute;
                bottom: 50px;
                left: 50%;
                transform: translateX(-50%);
            }
            .item{
                position: relative;
                &::after{
                    position: absolute;
                    content: '';
                    background-image: url(images/congthucchatluong/giayrach.png);
                    height: 80px;
                    width: 100%;
                    z-index: 999;
                    left: 0;
                    bottom: 0;
                }
                &::before{
                    content: '';
                    position: absolute;
                    height: 100%;
                    width: 100%;
                    background-color: #000;
                    opacity: 0.15;
                    top: 0;
                    left: 0;
                }
            }
        }
    }

    &__content{
        max-width: 670px;
        position: absolute;
        top: 51%;
        left: 50%;
        transform: translate(-50%,-50%);
        z-index: 95;
        background-image: url(images/TopBar_Banner/box.png);
        background-size: 100% 100%;
        background-repeat: no-repeat;
        padding: 50px 75px;
        text-align: center;
        .btn-discover{
            span{
                padding: 0 30px;
            }
        }
    }

    &__main{
        &--title{
            h1{
                font-family: Averta-bold;
                font-size: 60px;
                color: #39451e;
                text-transform: uppercase;
                margin: 0;
            }
        }
        &--description{
            padding: 0 30px;
            p{
                margin-bottom: 35px;
                line-height: 1.3;
                color: #414141;
            }
        }
    }
}

