.quality{
    background-image: url(images/congthucchatluong/bgr.png);
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;

    &::after{
        content: '';
        position: absolute;
        height: 75px;
        bottom: 0;
        left: 0;
        width: 100%;
        background-image: url(images/congthucchatluong/giayrach.png);
    }

    &::before{
        content: '';
        position: absolute;
        height: 75px;
        top: -50px;
        left: 0;
        width: 100%;
        background-image: url(images/congthucchatluong/giayrach1.png);
    }

    .progress-bar {
        margin: 0 0 60px;
        overflow: hidden;
        padding-right: 24px; /* Separate bars from container */
        background-color: inherit;
    }
    
    .progress-title-holder {
        position: relative;
        color: #39451e;
        font-family: Averta-bold;
        text-align: left;
        margin-bottom: 10px;
    }
    
    .progress-title {
        z-index: 100;
    }
    
    .progress-number-wrapper {
        width: 100%;
        z-index: 10;
    }
    
    .progress-number-mark {
        margin-bottom: 4px;
        border-radius: 3px;
        // background-color: rgb(22,23,29);
        padding: 0 8px;
        position: absolute;
        bottom: 0;   
        -moz-transform: translateX(-50%);
        -o-transform: translateX(-50%);
        transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        -webkit-transform: translateX(-50%); 
    } 
    
    .progress-number-wrapper,
    .progress-number-mark {
        letter-spacing: 0px;
        text-transform: none;
        color: #3f3f3f;
        font-family: Averta-regularitalic;
    }
    
    .down-arrow {
        border-left: 3px solid transparent;
        border-right: 3px solid transparent;
        // border-top: 3px solid #16171d;
        position: absolute;
        left: 50%;
        top: 100%;
        -moz-transform: translateX(-50%);
        -o-transform: translateX(-50%);
        transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        -webkit-transform: translateX(-50%);  
    }
    
    .progress-content-outter {
        height: 12px;
        background-color: #ffffff;
    }
    
    .progress-content {
        height: 12px;
        background-color: #39451e;
        width: 0%; /* Initial value */
    }

    &__formula{
        padding: 120px 0;
    }

    &__content{
        padding: 60px 0 60px 100px;
        &--title{
            p{
                text-align: left;   
            }
        }

        &--description{
            margin-bottom: 40px;
        }

        .border-tree{
            margin-bottom: 25px;
            &::after{
                transform: translateX(0);
                left: 0;
            }
        }
    }
}

