.mobile-menu{
    &__content{
        background-color: #39451e;
        position: fixed;
        top: 0;
        width: 270px;
        left: -270px;
        transition: all 0.5s ease;
        height: 100vh;
        overflow-x: hidden;
        z-index: 999;
    }
    &__bg{
        content: '';
        position: fixed;
        width: 100%;
        height: 100%;
        background-color: rgba($color: #000000, $alpha: 0.3);
        top: 0;
        left: 0;
        display: none;
    }
    &__top{
        position: relative;
        p{
            margin: 0;
            line-height: 50px;
            height: 50px;
            border-bottom: 1px solid #282828;
            padding: 0 15px;
            color: #ffffff;
        }

        &--button{
            position: absolute;
            right: 0;
            top: 0;
            a{
                height: 49px;
                line-height: 48px;
                display: block;
                width: 49px;
                text-align: center;
                border-left: 1px solid #282828;
                font-size: 18px;
                color: #fff;
                text-decoration: none;
                &:hover{
                    background-color: #0e7d1b;
                }
                
            }
        }
    }

    &__content2{
        &>ul{
            border-bottom: 1px solid #282828;
        }
        ul{
            margin: 0;
            padding: 0;
            list-style: none;
            a{
                line-height: 50px;
                height: 50px;
                display: block;
                color: #fff;
                text-transform: uppercase;
                padding: 0 15px;
                border-top: 1px solid #282828;
                text-decoration: none;
                background-color: #39451e;
                &:hover{
                    background-color: #0e7d1b;
                }
            }
        }
    }

    &__social{
        ul{
            font-size: 0;
            li{
                width: 50%;
                a{
                    font-size: 14px;
                    line-height: 50px;
                    height: 50px;
                    text-align: center;
                    color: #fff;
                    display: block;
                    border-bottom: 1px solid #282828;
                }
            }

            li+li{
                a{
                    border-left: 1px solid #282828;
                }
            }
        }
    }

    .sub-menu{
        display: none;
        a{
            background-color: #385001;
            &:hover{
                background-color: #0e7d1b;
            }
        }
    }

    .button-menu{
        margin-right: -15px;
        text-align: center;
        img{
            width: 14px;
            transform: rotate(90deg);
            transition: all 0.3s ease;
        }
    }

    .button-menu.active{
        img{
            transform: rotate(0);
        }
    }
}

