.news-item {
    &__content{
        
        &--title{
            a{
                font-size: 22px;
                font-family: Averta-bold;
                color: #39451e;
                text-transform: uppercase;
                height: 50px;
                margin: 30px 0 10px;
                line-height: 1.15;
            }
        }
        &--date{
            font-family: Averta-regularitalic;
            font-size: 16px;
            color: #666;
            margin: 0;
            .green{
                color: #9cac3b;
            }
        }
        &--description{
            color: #3f3f3f;
            height: 70px;
            line-height: 1.3;
            margin: 20px 0 25px;
        }

        &--button{
            text-transform: uppercase;
            color: #39451e;
            font-family: Averta-bold;
            &:hover{
                color: #39451e;
            }
        }

        &--img{
            img{
                width: 370px;
                height: 238px;
            }
        }
    }
}
