.contact-item {
    &:nth-child(2n){
        .col-lg-4{
            order: 2;
        }

        .col-lg-8{
            order: 1;
        }
    }
    margin-bottom: 30px;
    &__content{
        background-image: url(images/contact/contact-bg.png);
        background-size: cover;
        background-repeat: no-repeat;
        padding: 50px 30px;
        .title-base{
            text-align: left;
        }
        .border-tree::after{
            left: 0;
            transform: translateX(0);
        }

        &--description{
            p{
                margin-bottom: 20px;
            }
        }

        &--infor{
            padding-left: 60px;
            padding-bottom: 25px;
            p{
                line-height: 1.3;
                margin: 0;
            }
            .location{
                position: relative;
                margin-bottom: 15px;
                p{
                    margin: 0;
                    line-height: 1.3;
                }
                &::before{
                    content: '';
                    position: absolute;
                    width: 40px;
                    height: 40px;
                    background-image: url(images/lienhe/iconlocation.png);
                    background-size: contain;
                    background-repeat: no-repeat;
                    left: -60px;
                    top: 0;
                }
            }

            .phone{
                p{
                    margin: 0;
                    line-height: 1.3;
                }
                margin-bottom: 15px;
                position: relative;
                &::before{
                    content: '';
                    position: absolute;
                    width: 40px;
                    height: 40px;
                    background-image: url(images/lienhe/iconphone.png);
                    background-size: contain;
                    background-repeat: no-repeat;
                    left: -60px;
                    top: 0;
                }
            }

            .mail{
                p{
                    margin: 0;
                    line-height: 40px;
                }
                position: relative;
                &::before{
                    content: '';
                    position: absolute;
                    width: 40px;
                    height: 40px;
                    background-image: url(images/lienhe/iconemail.png);
                    background-size: contain;
                    background-repeat: no-repeat;
                    left: -60px;
                    top: 0;
                }
            }
        }
    }
}
