.shop-item {
    a{
        line-height: 41px;
        height: 42px;
        display: block;
        text-decoration: none;
        &:hover{
            color: #9cac3b;
        }
    }

    &>a{
        text-transform: uppercase;
        position: relative;
        i{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 13px;
            transition: all 0.3s ease;
        }
    }

    .submenu{
        display: none;
        padding-left: 20px;
        li+li{
            a{
                border-top: 1px solid #d9d9d9;
            }
        }
        li:first-child{
            a{
                border-top: none;
            }
        }

        a.active{
            color: #9cac3b;
        }
    }
}

.shop-item.active{
    &>a{
        border-bottom: 1px solid #d9d9d9;
        color: #9cac3b;
        i{
            transform: rotate(90deg);
            top: 30%;
            
        }
    }

}

.shop-item2{
    &>a{
        text-transform: lowercase;
        letter-spacing: -1px;
    }
}
.shop-item+.shop-item{
    a{
        border-top: 1px solid #d9d9d9;
    }
}

.product-selling{
    display: flex;
    margin-top: 20px;
    p{
        margin: 0;
    }

    &__infor{
        padding-left: 20px;
        padding-top: 10px;
        &--name{
            font-family: Averta-bold;
            color: #414141;
            line-height: 1.2;
        }

        .price{
            font-family: Averta-bold;
            font-size: 22px;
            color: #9cac3b;
            .unit{
                color: #39451e;
                font-size: 18px;
            }
        }
    }
}