.hot-product-item {
    margin-top: 30px;
    padding: 7px 7px 10px;
    border: 3px dashed #dadada;
    transition: all 0.3s ease;
    &:hover{
        // border-color: #39451e;
        border-style: solid;
    }

    &__img{
        position: relative;
        overflow: hidden;
        &>img{
            width: 271px;
            height: auto;
        }
        &--bg{
            position: absolute;
            width: 100%;
            height: 100%;
            top: 100%;
            left: 0;
            text-align: center;
            background-color: rgba(255, 255, 255, 0.35);
            transition: all 0.3s ease;
            img{
                position: relative;
                top: 50%;
                transform: translateY(-50%);
            }
        }
        &--tag{
            position: absolute;
            top: 12px;
            right: 8px;
            p{
                text-transform: uppercase;
                color: #fff;
                font-size: 16px;
                margin: 0;
            }
        }

        .tag-out{
            background-image: url(images/tag/bg-out.png);
            padding: 7px;
            background-size: cover;
            background-repeat: no-repeat;
        }
        .tag-new{
            background-image: url(images/tag/bg-new.png);
            padding: 7px;
            background-size: cover;
            background-repeat: no-repeat;
        }
        .tag-sale{
            background-image: url(images/tag/bg-sale.png);
            padding: 7px;
            background-size: cover;
            background-repeat: no-repeat;
        }
        .tag-hot{
            background-image: url(images/tag/bg-hot.png);
            padding: 7px;
            background-size: cover;
            background-repeat: no-repeat;
        }
    }

    &__content{
        &--title{
            a{
                color: #414141;
                font-family: Averta-bold;
                font-size: 18px;
                text-align: center;
                display: block;
                text-decoration: none;
                margin-top: 18px;
            }
        }
        &--price{
            p{
                font-family: Averta-bold;
                font-size: 30px;
                color: #9cac3b;
                text-align: center;
                line-height: 1.1;
                margin: 11px 0 0;
            }

            span{
                color: #39451e;
                font-size: 22px;
            }
        }

        &--unit{
            p{
                font-family: Averta-regularitalic;
                font-size: 16px;
                text-align: center;
                color: #bfbfbf;
                margin: 0;  
            }
        }
    }
}

.hot-product-item:hover .hot-product-item__img--bg{
    top: 0;
}