.faq-item {
    &__tab{
        p{
            margin: 20px 0 0;
            padding: 0 24px;
            height: 50px;
            line-height: 46px;
            border: 2px solid #fff;
            color: #fff;
            font-family: Averta-bold;
            max-width: 535px;
            cursor: pointer;
            transition: all 0.35s ease;
            &:hover{
                color: #39451e;
                background-color: #fff;
            }
        }
    }

    &__content{
        display: none;
        p{
            margin: 12px 0 0;
            padding: 15px 24px 20px;
            border: 2px dashed #fff;
            color: #fff;
            max-width: 535px;
        }
    }

    &:first-child{
        .faq-item__content{
            display: block;
        }
    }
}
.faq-item__tab.active p{
    color: #39451e;
    background-color: #fff;
}